import { Link } from "react-router-dom"
import { BASE_URL } from "../App"
import { GoHome } from "react-icons/go"
import { BiListPlus } from "react-icons/bi"
import { AiOutlineSearch } from "react-icons/ai"
import { IoSettingsOutline } from "react-icons/io5"

export default function Navi() {
    return (
        <div className="bg-gray-300">
            <nav>
                <div className="flex justify-center">
                    <div className="m-4">
                        <div class="rounded-3xl shadow-2xl h-20 w-20 p-4  bg-gray-200">
                            <Link to={BASE_URL}><GoHome size={30} color={'#ccc'} /></Link>
                        </div>
                    </div>
                    <div className="m-4">
                        <div class="rounded-3xl shadow-2xl h-20 w-20 p-4  bg-gray-200">
                            <Link to={`${BASE_URL}content`}><BiListPlus size={30} /></Link>
                        </div>
                    </div>
                    <div className="m-4">
                        <div class="rounded-3xl shadow-2xl h-20 w-20 p-4  bg-gray-200">
                            <Link to={`${BASE_URL}picture`}><AiOutlineSearch size={30} /></Link>
                        </div>
                    </div>
                    <div className="m-4">
                        <div class="rounded-3xl shadow-2xl h-20 w-20 p-4  bg-gray-200">
                            <Link to={`${BASE_URL}loading`}><IoSettingsOutline size={30} /></Link>
                        </div>
                    </div>
                </div>
            </nav>
        </div>
    )
}